import tagSexAge from './tag-sex-age.vue';

export default {
	components: {
		tagSexAge
	},
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu
		};
	},
	methods: {
		guanzhu() {
			this.isguanzhu = true;
			uni.showToast({
				title: '关注成功!'
			});
		}
	}
};
